* {
    box-sizing: border-box;
  }
  


  
  body {
    background-image: url(./884f4-triangle.png) ;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: "Montserrat", sans-serif;
    height: 100vh;
    margin: -20px 0 50px;
  }
  
  h1 {
    font-weight: bold;
    
    margin: 0;
    margin-bottom: 20px;
  }
  
  h2 {
    text-align: center;
  }
  
  p {
    font-size: 14px;
    font-weight: 100;
    line-height: 20px;
    letter-spacing: 0.5px;
    margin: 20px 0 30px;
  }
  
  span {
    font-size: 12px;
  }
  
  a {
    color: #5860a7;
    font-size: 14px;
    text-decoration: none;
    margin: 15px 0;
  }
  
  button {
    border-radius: 20px;
    border: 5px solid #40e830;
    background-color: #2600ff;
    color: #4be608;
    font-size: 12px;
    margin-top: 25px;
    font-weight: bold;
    padding: 12px 45px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: transform 80ms ease-in;
  }
  button:hover{
    scale: 1.2;
    color:rgb(248, 0, 74);
    clip: auto;
  }
  
  
  button:active {
    transform: scale(0.95);
  }
  
  button:focus {
    outline: none;
  }
  
  button.ghost {
    background-color: transparent;
    border-color: #ea02c0;
  
  }
  
  form {
  background: #10464f;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 50px;
    height: 100%;
    text-align: center;
  }
  
  inputz {
  
    color: rgb(222, 237, 19);
    border: none;    
    outline: none;

    background-color: transparent;
    padding: 12px 15px;
    margin: 8px 0;
    width: 100%;
  }
  inputz::placeholder{
    color: #18de5d;
  }


  inputz:focus{
    border: 2px solid #04c804;
    border-radius: 25px 10px;
    transform:translateY(5px) ;
  }
  
  .container {
    background: url(./hanma.gif) no-repeat center center fixed;
    opacity: 0.9;
    background-size: cover;
    border-radius: 10px;
    box-shadow: 14px 14px 28px rgba(215, 11, 11, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    position: relative;
    overflow: hidden;
    width: 768px;
    max-width: 100%;
    min-height: 480px;
  }
  
  .form-container {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
  }
  
  .sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
  }
  
  .container.right-panel-active .sign-in-container {
    transform: translateX(100%);
  }
  
  .sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
  }
  
  .container.right-panel-active .sign-up-container {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: show 0.6s;
  }
  
  @keyframes show {
    0%,
    49.99% {
      opacity: 0;
      z-index: 1;
    }
  
    50%,
    100% {
      opacity: 1;
      z-index: 5;
    }
  }
  
  .overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.6s ease-in-out;
    z-index: 100;
  }
  
  .container.right-panel-active .overlay-container {
    transform: translateX(-100%);
  }
  
  .overlay {
    background: #e88e07;
    background: -webkit-linear-gradient(to right, #222529, #222529);
    background: linear-gradient(to right, #222529, #222529);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    color: #c21313;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
  }
  
  .container.right-panel-active .overlay {
    transform: translateX(50%);
  }
  
  .overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    transform: translateX(0);
    transition: transform 0.6s ease-in-out;
  }
  
  .overlay-left {
    transform: translateX(-20%);
  }
  
  .container.right-panel-active .overlay-left {
    transform: translateX(0);
  }
  
  .overlay-right {
    right: 0;
    transform: translateX(0);
  }
  
  .container.right-panel-active .overlay-right {
    transform: translateX(20%);
  }

  @media screen and (max-width: 768px) {
    /* Apply styles only for screens with a maximum width of 768px (common for mobile) */
    body {
    display: block;
    overflow: hidden;
    height: 100vh;
    }
  }

  
  
  
  